@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:"Poppins",sans-serif;position:relative;overflow:hidden}

.top-navbar{display:flex;width:100%;flex-direction:row;margin:0px;z-index:2;box-shadow:3px 3px 5px #8b8b8b;position:relative;background-color:#fff}.top-navbar .navbar-title{width:80%;display:flex;flex-direction:row}.top-navbar .navbar-title .arrow-left{margin:0;padding:20px}.top-navbar .navbar-title .dashboard{margin:0;padding:16px 0px 0 15px;font-size:16px;color:#858585;font-weight:400}.top-navbar .navbar-title .chevron-right{padding:20px  0px 0px 10px}.top-navbar .navbar-title .social-feed{margin:0;padding:16px 10px;font-size:16px;font-weight:400}.top-navbar .navbar-icons{width:20%;display:flex;position:relative;justify-content:flex-end}.top-navbar .navbar-icons .icon{display:none;margin:auto 0px;width:70px;flex-direction:column;background-color:transparent;border:none;outline:none;cursor:pointer}.top-navbar .navbar-icons .icon .icon-bar{margin-bottom:3px;background-color:#000;width:22px;height:3px;transition:all .5s}.top-navbar .navbar-icons .icon .icon-bar.active:nth-child(1){transform:rotate(45deg);transform-origin:10% 10%}.top-navbar .navbar-icons .icon .icon-bar.active:nth-child(2){opacity:0}.top-navbar .navbar-icons .icon .icon-bar.active:nth-child(3){transform:rotate(-45deg);transform-origin:10% 90%}.top-navbar .navbar-icons .bars{margin:0;display:none;padding:18px 35px;font-size:20px;font-weight:400;position:relative;right:0;cursor:pointer;background-color:#fff;z-index:1}.top-navbar .navbar-icons .times{margin:0;display:none;padding:14px 35px;font-size:30px;font-weight:400;position:absolute;top:0;cursor:pointer}.top-navbar .navbar-icons .bars.hide{display:none}@media (max-width: 790px){.top-navbar .navbar-icons .bars,.top-navbar .navbar-icons .times,.top-navbar .navbar-icons .icon{display:flex}}

.left-navbar{position:relative;display:flex;padding:20px 0;width:350px;box-sizing:border-box;height:100%;flex-direction:column;overflow:hidden;overflow-y:scroll}.left-navbar::-webkit-scrollbar{display:none}.left-navbar .user{display:flex;margin-bottom:10px;padding:0 40px}.left-navbar .user .user-image{width:40px;height:40px;border-radius:50%}.left-navbar .user .user-name{margin:8px 0px 8px 18px;font-size:16px;font-weight:500}.left-navbar .section-1,.left-navbar .section-2{display:flex;flex-direction:column;padding:0}.left-navbar .section-1 .options,.left-navbar .section-2 .options{display:flex;width:100%;flex-direction:row;justify-content:flex-start;padding:5px 0px;line-height:1;cursor:pointer}.left-navbar .section-1 .options .navbar-img,.left-navbar .section-2 .options .navbar-img{margin:0;width:25%;display:block;display:flex;margin-left:10px;justify-content:center;font-size:22px}.left-navbar .section-1 .options .navbar-title,.left-navbar .section-2 .options .navbar-title{margin:0;padding-top:3px;font-size:14px;line-height:1;width:50%;display:flex;font-weight:400}.left-navbar .section-1 .options .navbar-number,.left-navbar .section-2 .options .navbar-number{margin:0;width:10%;display:flex;line-height:1;color:#999999;justify-content:flex-end;font-size:14px}.left-navbar .section-1 .options:nth-child(1),.left-navbar .section-2 .options:nth-child(1){background-color:#762754}.left-navbar .section-1 .options:nth-child(1) .navbar-title,.left-navbar .section-2 .options:nth-child(1) .navbar-title{color:#fff}.left-navbar .section-1 .options:nth-child(1) .navbar-img,.left-navbar .section-2 .options:nth-child(1) .navbar-img{color:#fff}.left-navbar .section-1 .see-more,.left-navbar .section-2 .see-more{display:flex;width:80%;align-self:center;flex-direction:row;justify-content:center;background-color:#e2e1e7;border-radius:7px;margin-top:8px;padding:8px;line-height:1;margin-bottom:8px;cursor:pointer}.left-navbar .section-1 .see-more .navbar-arrow,.left-navbar .section-2 .see-more .navbar-arrow{margin:0;font-size:14px;font-weight:500}.left-navbar .section-1 .see-more .navbar-img,.left-navbar .section-2 .see-more .navbar-img{color:#000000;margin-left:8px;font-size:14px;padding:2px 0}.left-navbar .section-1 .topic,.left-navbar .section-2 .topic{margin:10px;margin-left:40px;font-size:16px}@media (max-width: 790px){.left-navbar{display:flex;position:absolute;right:-350px;z-index:1;background-color:#fff;overflow-y:scroll;transition:right .5s ease-in-out}.left-navbar.active{right:0px}}

.cards{position:relative;display:flex;width:calc(100% - 350px);align-content:center;justify-content:center;background-color:#e2e1e7;overflow:auto;height:100%;opacity:1;transition:opacity 0.5s ease-in}.cards .overlay{position:absolute;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,0);transition:background-color 0.5s ease-in,z-index 0.2s ease-in;z-index:-1}.cards .overlay.active{z-index:0;background-color:rgba(0,0,0,0.5)}.cards .wrapper{margin:0 auto;width:50%;min-width:300px;display:flex;flex-direction:column}.cards .wrapper .fill-card{margin-top:20px;width:100%;padding:15px;box-sizing:border-box;display:flex;flex-direction:column;background-color:#fff;border-radius:20px;box-shadow:3px 3px 5px #7a7a7a}.cards .wrapper .fill-card .top-fill-card{display:flex}.cards .wrapper .fill-card .top-fill-card .fill-card-image{width:40px;border-radius:50%;height:40px}.cards .wrapper .fill-card .top-fill-card form{width:100%;display:flex}.cards .wrapper .fill-card .top-fill-card form .fill-card-box{display:flex;margin-left:15px;background-color:#e2e1e7;border-color:transparent;font-family:"Poppins";outline:none;border-radius:20px;padding:8px 15px;line-height:20px;box-sizing:border-box;width:100%;height:auto;font-size:14px;resize:none;overflow:auto}.cards .wrapper .fill-card .top-fill-card form .fill-card-box::-webkit-scrollbar{display:none}.cards .wrapper .fill-card .bottom-fill-card{display:flex;margin-top:15px;margin-left:12%;margin-right:3%;justify-content:space-between}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media{display:flex;justify-content:space-between;cursor:pointer;margin-right:5px}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-icon{line-height:1}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-title{margin:0;margin-left:7px;padding:5px 0px;box-sizing:border-box;text-align:left;font-size:12px;font-weight:400;line-height:1}@media (max-width: 1100px){.cards .wrapper{min-width:70%}.cards .wrapper .fill-card .bottom-fill-card{margin:10px 5px 0 5px}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media{flex-direction:row}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-icon,.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-title{align-self:center;text-align:center}}@media (max-width: 850px){.cards .wrapper .fill-card .bottom-fill-card .fill-card-media{flex-direction:column}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-icon,.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-title{align-self:center;text-align:center}}@media (max-width: 790px){.cards{width:100vw}.cards .wrapper{min-width:75vw}.cards .wrapper .fill-card .bottom-fill-card{margin:8px 8px 0 8px}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media{flex-direction:row}}@media (max-width: 480px){.cards .wrapper .fill-card .bottom-fill-card .fill-card-media{flex-direction:column}.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-icon,.cards .wrapper .fill-card .bottom-fill-card .fill-card-media .media-title{align-self:center;text-align:center}}

.cards .wrapper .show-card{width:calc(100% - 30px);margin-top:20px;display:flex;padding:15px;flex-direction:column;background-color:#fff;border-radius:20px;box-shadow:3px 3px 5px #7a7a7a}.cards .wrapper .show-card:nth-last-child(1){margin-bottom:20px}.cards .wrapper .show-card .top-show-card{display:flex}.cards .wrapper .show-card .top-show-card .show-card-image{width:70px;display:block}.cards .wrapper .show-card .top-show-card .show-card-image img{width:50px;height:50px;border:3px solid #762754;border-radius:50%}.cards .wrapper .show-card .top-show-card .show-card-user-details{display:flex;flex-direction:column;width:90%;margin-left:8px}.cards .wrapper .show-card .top-show-card .show-card-user-details .user-name{margin:0;font-size:16px;line-height:1;font-weight:500}.cards .wrapper .show-card .top-show-card .show-card-user-details .user-role{margin:0;font-size:14px;font-weight:300}.cards .wrapper .show-card .top-show-card .show-card-user-details .user-time{margin:0;font-size:12px;font-weight:300}.cards .wrapper .show-card .top-show-card .show-card-share .share{margin:0;font-size:16px;line-height:1;font-weight:700;cursor:pointer}.cards .wrapper .show-card .bottom-show-card{width:100%}.cards .wrapper .show-card .bottom-show-card .show-card-content{margin:0;margin-top:10px;font-size:14px;font-weight:400;width:100%}@media (max-width: 1100px){.cards .wrapper .show-card .top-show-card .show-card-user-details{margin-left:10px}}

.main-container{display:flex;position:relative;width:100%;height:calc(100vh - 57px)}

